import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, LoadingController, AlertController, ToastController, NavParams } from "ionic-angular";
import { UserProvider } from '../../providers/user/user';
import { OrderByPipe } from "./orderby.pipe";
import { Navbar } from 'ionic-angular';
import { TranslatePipe, TranslateService, LangChangeEvent } from '@ngx-translate/core';
@IonicPage({
  name: 'page-chat',
  segment: 'chats'
})

@Component({
  selector: 'page-chat',
  templateUrl: 'chat.html'
})
export class ChatPage {

  data3: any;
  length: any;

  constructor(public navCtrl: NavController, public translate: TranslateService, public user: UserProvider, public forgotCtrl: AlertController, public navparams: NavParams, public loadingCtrl: LoadingController, public toastCtrl: ToastController) {

    this.translate.setDefaultLang('en');
    this.translate.use('es');
    this.checkAllData();
    // this.user.supportlist().subscribe(
    //   data => {
    //     let datas = JSON.parse(JSON.stringify(data));
    //     let data2 = JSON.parse(datas._body);
    //     console.log(data2);
    //     if (data2.success) {

    //       this.data3 = data2.data;
    //       console.log(this.data3);


    //     }

    //   }
    // )

  }

  ionViewWillEnter() {
    this.supportlist();
  }
  checkAllData() {
    this.user.userInfo().subscribe(
      data => {
        let datas = JSON.parse(JSON.stringify(data));
        let data2 = JSON.parse(datas._body);
        
        if (data2.success == false) {
          console.log('Dismissed toast');
          localStorage.removeItem('currentUser');
          localStorage.removeItem('MemberId');
          localStorage.removeItem("token");
          localStorage.removeItem('2fa');
          localStorage.removeItem('chekout');
          localStorage.removeItem('VerificationLevel');
          this.navCtrl.setRoot('page-auth');

        } else {



        }

      }, error => {
        console.log(error.JSON);
        setTimeout(() => {
          this.navCtrl.setRoot('page-general_error');
        }, 1000);
      },
    )
  }



  ngOnInit() {

  }


  supportlist() {
    this.user.supportlist().subscribe(
      data => {
        let datas = JSON.parse(JSON.stringify(data));
        let data2 = JSON.parse(datas._body);
        console.log(data2);

        if (data2.success) {

          this.data3 = data2.data;
          this.length = this.data3.length;
          // alert(this.length);
          console.log(this.data3);


        }

      }
    )
  }


  openChat(item) {
    // console.log(proptype);
    this.navCtrl.push('page-chat-detail', { 'item': item });
  }

  goPage() {

    this.navCtrl.push('page-message');

  }

}



